body {
  margin: 0;
  font-size: 16px;
  font-weight: 300;
  font-family: 'FuraCode Nerd Font', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  min-height: 100vh;
}

.text-backdrop {
  background: rgb(246, 36, 89);
  color: white;
  width: 100%;
  padding: 25px 0;
  z-index: 1;

  min-height: 180px;
  box-shadow: 1px 2px 2px rgba(246, 36, 89, 0.8);
}

.text-container {
  margin: 0 auto;
  max-width: 800px;
}

.result,
.reply-sentence {
  font-weight: 300;
  font-size: 2.8em;
  line-height: 1.5em;
}

.reply-sentence {
  margin-top: 0.8em;
}

.listening-indicator {
  right: 40px;
}

.random-sound-clip {
  left: 40px;
}

.listening-indicator,
.random-sound-clip {
  position: absolute;
  top: 20px;

  font-size: 3.8em;
  cursor: pointer;
}

.trump-cover {
  background-image: url(https://hips.hearstapps.com/hmg-prod.s3.amazonaws.com/images/south-park-donald-trump-1505159129.png);
  background-repeat: no-repeat;
  background-size: cover;
}

.bg-blur-backdrop {
  position: absolute;
  width: 100%;
  height: 100%;
  backdrop-filter: blur(10px);
  z-index: 0;
}

.chat-input {
  background: rgba(255, 255, 255, 0.8);
  border: none;
  position: fixed;
  bottom: 50px;
  min-width: 300px;

  padding: 10px 32px;
  font-size: 24px;
  color: #f62559;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.28);
  font-weight: 300;

  border-radius: 10em;
  border-bottom: 2px solid #f62559;
  outline: none;

  z-index: 1;
}
